import { Node, Inline, Injection } from '@ali/4ever-cangjie';
import { LinkNode } from '../types';

export function isLinkCardNode(node: Node, injections?: Injection[]): node is LinkNode {
  if (isLinkNode(node)) {
    const { data } = node;
    if (data && data.cardInfo?.displayType === 'card') {
      return true;
    }
  }
  // node 注入有 displayType
  if (injections && injections.length > 0) {
    const injection = injections.find((i) => i.key === node.key);
    if (injection && injection.data?.cardInfo?.displayType === 'card') {
      return true;
    }
  }

  return false;
}

export function isLinkNode(node: any): node is LinkNode {
  if (node && Inline.isInline(node) && node.type === 'link') {
    return true;
  }

  return false;
}
